@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&family=Roboto+Condensed:wght@300;400;700&display=swap);
:root {
  --primary-dark: #5651e5;
  --primary-light: #709dff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Poppins", sans-serif;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
}
h2 {
  font-size: 2rem;
}

.container {
  max-width: 1240px;
  margin: auto;
  padding: 0 1rem;
}

button {
  background-image: linear-gradient(45deg,#5651e5 0%, #709dff 100%);
  background-image: linear-gradient(45deg,var(--primary-dark) 0%, var(--primary-light) 100%);
  border: 1px solid #5651e5;
  border: 1px solid var(--primary-dark);
  color: #fff;
  padding: .7rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
}



@media screen and (max-width: 940px) {
  h1 {
    font-size: 2.5rem;
  }
  h2 {
    font-size: 1.5rem;
  }
}



.container {
  padding: 3rem 0;
}

.carousel img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.hbold {
  text-align: center;
}

@media (max-width: 768px) {
  .hbold {
    font-size: 24px; /* Adjust the font size for mobile */
  }
}

.about {
  width: 100%;
  text-align: center;
  padding: 4rem 0;
}

.about h1 {
  padding: 1rem;
}

.img-container {
  --gap: 1rem;
  --num-cols: 5;
  --row-height: 300px;

  display: grid;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  margin: auto;
  padding: 1rem 0;
  grid-gap: var(--gap);
  gap: var(--gap);
}
.seal {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 5px;
  -webkit-column-gap: 5px;
          column-gap: 5px;
}

.about img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.seal img {
  width: 100%;
  height: 100%;
}

.span-3 {
  grid-column: span 3;
}

.image-grid-row-2 {
  grid-row: span 2;
}

@media screen and (max-width: 940px) {
  .img-container {
    --num-cols: 2;
    --row-height: 200px;
    --grap: 0.5rem;
  }

  .span-3 {
    grid-column: span 2;
  }
}

.btn {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.button {
  flex: 0 0 60%;
  margin-bottom: 1rem;
  margin-top: 1rem;
}

.footer {
  width: 100%;
  padding: 2rem 0;
  background-color: #333;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo {
  margin-bottom: 1rem;
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  background: none; /* Remove the background */
}


.text {
  display: flex;
  align-items: center;
}

.footer h3 {
  color: var(--primary-light);
}

.hero {
    width: 100%;
    height: 100vh;
    color: #fff;
    position: relative;
}

.overlay {
    background-color: rgba(0, 0, 0, 0.1);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

#video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.hero .content {
    position: absolute;
    top: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: 100%;
    margin: auto;
    padding: 1rem;
}

.form {
    display: flex;
    background-color: #ffffffcc;
    color: #000;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #ccc;
    padding: 4px;
    border-radius: 8px;
    max-width: 700px;
    width: 100%;
    margin: 0 auto;
}

.form input[type=text] {
    border: transparent;
    background-color: transparent;
    width: 400px;
    font-size: 1.2rem;
    font-family: 'Poppins',sans-serif;
}

.form input[type=text]:focus {
    outline: none;
}

@media screen and (max-width: 940px) {
    .form input[type=text] {
        max-width: 275px;
    }

}
.navbar {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    z-index: 2;
    position: absolute;
    color: #fff;
    
}

.navbar-bg {
    background-color: #f5f5f5;
}

.nav-menu {
    display: flex;
}

li {
    padding: 0 1rem;
}

.hamburger {
    display: none;
    /* padding: 1rem; */
}

.mobile-menu {
    display: none;
}

.icon {
    font-size: 1.5rem;
    cursor: pointer;
}


@media screen and (max-width: 940px) {

    .mobile-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        top: -100vh;
        left: 0;
        position: absolute;
        justify-content: space-between;
        /* align-items: center; */
        /* color: rgba(255, 255, 255, .95); */
        background-color: #f5f5f5;
        transition: 0.3s;
        color: #000;
    }

    .dark {
        color: #000;
    }

    .active {
        top: 80px;
    }

    .mobile-menu li {
        padding: 1.2rem 1rem;
        margin: 0 1rem;
        border-bottom: 1px solid #ccc;
    }

    .mobile-nav {
        margin: 1rem 0;
    }

    .mobile-menu-bottom {
        width: 100%;
        text-align: center;
        padding: 1rem 0;
    }

    .social-icons {
        display: flex;
        justify-content: space-around;
        margin: 2rem 0;
    }

    .social-icons .icon:hover {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        transition: 0.3s;
    }

    .mobile-menu button {
        margin: 1rem;
        width: 90%;
    }


    .nav-menu {
        display: none;
    }

    .nav-icons {
        display: none;
    }
    .hamburger {
        display: block;
    }

}
.touristicplaces .container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}

@media screen and (max-width: 100%) {
  .views .container {
    grid-template-columns: 2fr;
  }
}

.touristicplaces img {
  height: 100%;
  width: 100%;
}

.touristicplaces-location {
  position: relative;
}

.touristicplaces-location .overlay {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background: rgba(0, 0, 0, 0.3);
}

.touristicplaces-location p {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  color: #fff;
  font-size: 2rem;
  font-weight: 500;
}

